$gutter:    24px;
$gutter-xs: 60px;
$gutter-tight: 10px;

$screen-xs: 0;
$screen-sm: 480px;
$screen-md: 768px;
$screen-lg: 992px;
$screen-xl: 1224px;

/* -------------------------------------------------- */

/* container */
.grid-container {
  margin-right: auto;
  margin-left: auto;
  padding-left: calc($gutter/2);
  padding-right: calc($gutter/2);
}
.grid-container--fluid {
  width: auto;
  margin-right: auto;
  margin-left: auto;
  padding-left: calc($gutter/2);
  padding-right: calc($gutter/2);
}
@media (max-width: 767px) {
  .grid-container {
    padding-left: calc($gutter-xs/2);
    padding-right: calc($gutter-xs/2);
  }
}

/* -------------------------------------------------- */

/* row */
.grid-row {
  margin-left: -calc($gutter / 2);
  margin-right: -calc($gutter / 2);
  &--tight {
    margin-left: -calc($gutter-tight / 2);
    margin-right: -calc($gutter-tight / 2);
    & [class^='col-'] {
      padding: 0 calc($gutter-tight / 2);
    }
  }
}

@media (max-width: 767px) {
  .grid-row {
    margin-left: -calc($gutter-xs / 2);
    margin-right: -calc($gutter-xs / 2);
    &--tight {
      margin-left: -calc($gutter-tight / 2);
      margin-right: -calc($gutter-tight / 2);
      & [class^='col-'] {
        padding: 0 calc($gutter-tight / 2);
      }
    }
  }
}

